  public matselect_colour_manipulatio(){

    let mat_placeholder = document.getElementsByClassName("mat-select-placeholder");
    Array.from(mat_placeholder).forEach((el) => {
      el["style"].color = "#d3d7dd";
    });

    let mat_form_underline = document.getElementsByClassName("mat-form-field-underline");
    Array.from(mat_form_underline).forEach((el) => {
      el["style"].display = "none";
    });

    let mat_form_field_label = document.getElementsByClassName("mat-form-field-label");
    Array.from(mat_form_field_label).forEach((el) => {
      el["style"].display = "none";
    });

    let mat_select_arrow = document.getElementsByClassName("mat-select-arrow");
    Array.from(mat_select_arrow).forEach((el) => {
      el["style"].color = "#d3d7dd";
    });

    let mat_select_value = document.getElementsByClassName("mat-select-value");
    Array.from(mat_select_value).forEach((el) => {
      el["style"].color = "#d3d7dd";
    });      

    let mat_placeholder_hide = document.getElementsByClassName("mat-form-field-hide-placeholder");
    Array.from(mat_placeholder_hide).forEach((el) => {
      el["style"].color = "#d3d7dd";
    });
  }
  
